@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
header {
	margin: 0 auto;
	text-align: center;
	padding-top: 200px;
}

header h1 {
	margin: 0;
	font-size: 3rem;
	color: blue;
}

header p {
	font-size: 1.75rem;
	font-weight: 700;
	font-style: italic;
	color: blue;
	font-family: 'Poppins', sans-serif;
}
.containerSlide{
	width: 420px;
	margin: auto;
}

.containerSlide img {
	height: 400px;
}
body {
	
	margin: 0;
	padding: 0;
	font-family: sans-serif;
	box-sizing: border-box;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", 
	monospace;
}



nav {
	display: flex;
	justify-content: center;
	background-color: #F7F783;
}

ul {
	list-style: none;
}

nav ul li {
	display: inline-block;
	margin-right: 35px;
}

nav ul li a {
	color: green;
	text-decoration: none;
}


